* {
    box-sizing: border-box;
}
a {
    text-decoration: none;
    display: inline-block;
}
ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
}
.container {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
}
body {
    //font-family: 'Roboto', sans-serif;
    font-family: 'PT Serif', serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #5e5e5e;
}
header {
    font-family: 'Roboto', sans-serif;
}
//Header top
.header__top {
    background: #303030;
}
.header__btn {
    float: right;
    line-height: 36px;
    font-family: 'Roboto', sans-serif;
    color: #1b1b1b;
    background: #FFC222;
    padding: 5px 20px 4px 50px;
    font-size: 16px;
    position: relative;
}
.header__btn:before {
    font-family: 'icomoon';
    content: "\f0b1";
    position: absolute;
    left: 22px;
    color: #1b1b1b;
}
.header__contacts:after { //Решение проблемы с float задается родителю!
    content: '';
    clear: both;
    display: block;
}
.header__phone, .header__email {
    color: #cbcacb;
    letter-spacing: 0.25px;
    line-height: 45px;
    position: relative;
}
.header__phone {
    padding-left: 20px;
    padding-right: 30px;
}
.header__phone:before {
    font-family: 'icomoon';
    content: "\f095";
    position: absolute;
    left: 0;
    color: #FFC222;
}
.header__email {
    padding-left: 24px;
}
.header__email:before {
    font-family: 'icomoon';
    content: "\f003";
    position: absolute;
    left: 0;
    color: #FFC222;
}
//Header content
.header__content {
    padding: 34px 0 23px;
}
.header__content-inner {
    display: flex;
    justify-content: space-between;
}
.menu li {
    display: inline-block;
}
.menu a {
    color: #323232;
    font-size: 16px;
    line-height: 36px;
    border: 1px solid transparent;
    padding: 0 16px;
    transition: all 0.3s;
}
.menu a:hover {
    border-color: #FFC222;
}
.menu li + li {
    padding-left: 10px; //Зделать отступы только внутри, все li, кроме первого, отступят друг от друга на 10px
}
#modal {
    display: none;
}
//Slider
.slider {
    background-image: url("../img/slider-fon.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 500px;
}
.slider__item-content {
    text-align: center;
    padding-top: 110px;
    max-width: 665px;
    margin: 0 auto;
}
.slider__title {
    text-transform: uppercase;
    font-size: 30px;
    line-height: 36px;
    color: #fff;
    font-weight: 500;
    letter-spacing: 1.5px;
    margin-bottom: 22px;
    font-family: 'Roboto', sans-serif;
}
.slider__text {
    line-height: 24px;
    font-size: 18px;
    color: #d3d2d2;
    margin-bottom: 50px;
}
.default__btn {
    font-size: 14px;
    line-height: 24px;
    color: #fff;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    padding: 8px 30px 8px 24px;
    border: 1px solid #FFC222;
    border-right-width: 20px;
    position: relative;
    outline: none;
}
.default__btn:after {
    content: "\f105";
    font-family: 'icomoon';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -12px;
}
.slider__inner {
    position: relative;
}
.slick-btn {
    position: absolute;
    top: 70%;
    z-index: 3;
    width: 40px;
    height: 40px;
    border: 1px solid #fff;
    cursor: pointer;
    outline: none;
    background-color: #FFC222;
}
.slick-btn.slick-disabled {
    background-color: transparent;
    cursor: default;
}
.slick-btn:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'icomoon';
    color: #fff;
}
.slick-next:before {
    content: "\f105";
}
.slick-prev:before {
    content: "\f104";
}
.slick-next {
    right: 0;
}
.slick-prev {
    left: 0;
}
//Section about
.services {
    padding: 50px 0 30px;
}
.services__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
}
.services__title-box {
    max-width: 520px;
    padding-left: 140px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        width: 100px;
        height: 2px;
        background-color: #FFC222;
        left: 0;
        top: 11px;
    }
}
.services__title {
    font-size: 24px;
    line-height: 24px;
    font-weight: 500;
    color: #212020;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 10px;
}
.services__text {
    letter-spacing: 0.1px;
}
.services__btn a {
    color: #373636;
    line-height: 24px;
    letter-spacing: 0.25px;
    padding: 5px 38px;
    border: 3px solid #e6e6e6;
}
.services__items {
    display: flex;
    justify-content: space-between;
}
.services__item {
    max-width: 360px;
    text-align: center;
    padding: 30px 0 80px;
    position: relative;
    min-height: 375px;
    background-color: #f9f9f9;
    box-shadow: 0 7px 24.3px 2.7px rgba(91, 89, 89, 0.14);
}
.services__item-title {
    color: #212020;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    padding: 24px 0;
}
.services__item-text {
    letter-spacing: 0.1px;
    padding: 0 30px 30px;
}
.services__item-btn {
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    & .default__btn {
        color: #373636;
        &:after {
            color: #fff;
        }
    }
}
.services__item-link {
    color: #050505;
    font-family: 'Roboto', sans-serif;
    border-bottom: 1px solid #050505;
    margin-right: 60px;
}